  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn {
  height: 42px;
  color: #000;
  background-color: #f8d24e;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 0;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  display: flex;
}

.btn:hover {
  color: currentColor;
  background-color: #0a8817;
  border-color: #0a8817;
}

.btn:focus {
  color: #fcfcfc;
  background-color: #0a8817;
  border-color: #0a8817;
  outline: none;
}

.btn:disabled {
  color: currentColor;
  background-color: #ababab;
  border-color: #ababab;
}

.btn_icon {
  stroke: currentColor;
}

.btn--readmore {
  min-width: 164px;
}

@media screen and (min-width: 768px) {
  .btn--readmore {
    animation-name: pulse-read;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  @keyframes pulse-read {
    0% {
      transform: scale(1);
    }

    50% {
      transform: scale(1.2);
    }

    100% {
      transform: scale(1);
    }
  }
}

@media screen and (max-width: 767px) {
  .btn--readmore {
    animation-name: pulse-read-mobile;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  @keyframes pulse-read-mobile {
    0% {
      transform: scale(1);
    }

    50% {
      transform: scale(1.05);
    }

    100% {
      transform: scale(1);
    }
  }
}

.btn--ourlocations {
  min-width: 196px;
